.detailContract {
    width: 100%;
    height: 100%;
    padding: 20px;
    background-color: #fff;
    &-title {
      display: flex;
      justify-content: space-between;
      .left {
        font-weight: 700;
      }
      .right {
        color: #096DD9;
        cursor: pointer;
      }
    }
    &-content {
      height: calc(100% - 60px);
      .ant-form {
        .ant-row {
          margin: 15px 20px 0;
        }
      }
      .upload-box {
        margin: 30px 0 0 20px;
      }
    }

    &-action {
      width: 100%;
      height: 100%;
      padding: 20px;
      background-color: #fff;
      .action-header {
        >span {
          font-weight: 600;
        }
      }
      .action-content {
        box-sizing: border-box;
        height: calc(100% - 80px);
        padding-top: 15px;
        margin: 10px 0;
        overflow: auto;
        .box {
          margin-bottom: 25px;
          .unite {
            color: #ccc;
            margin-right: 5px;
          }
        }
      }
    }
}
@primary-color: #1890ff;